<template>
     <div class="public_content">
        <div class="public_title"><span>团队成员-添加经纪人</span></div>

        <div class="public_tip orange"><i class="iconfont">&#xe66d;</i><span>提示：经纪人必须注册本站帐号，且未加入任何门店</span></div>

        <v-code label='现任店长手机号：' ref="old"></v-code>
        <v-code label='新任店长手机号：' ref='new'></v-code>
        
        <el-button type="primary" round @click="onSubmit" class="public_submit">确认更换</el-button>
     </div> 
</template>

<script>
import {changeShopowner} from '@/api/shop'
import vCode from "@/components/code/index";
export default {
  data() {
    return {
      new: false,
      old: false
    };
  },
  components: {
    vCode
  },
  computed: {},
  methods: {
    onSuccess(val) {
      this[val] = true;
    },
    onSubmit() { 

        if (this.$refs.old.form.code && this.$refs.old.form.phone && this.$refs.new.form.phone && this.$refs.new.form.code) {
          let data = {
            old_phone: this.$refs.old.form.phone,
            old_code: this.$refs.old.form.code,
            new_phone: this.$refs.new.form.phone,
            new_code: this.$refs.new.form.code
          }
          changeShopowner(data).then(res => {
            if (res.data.code == 1) {
              this.$message.success('更改成功')
            }
          })
        }  else {
          this.$message.error('请将信息填写完整')
        }
    }
  }
};
</script>

<style lang='scss' scoped>
.public_tip {
  margin: 30px 0;
}
</style>



